// Write your code here
import {Component} from 'react'

import './index.css'

class Stopwatch extends Component {
  state = {
    time: 0,
    running: false,
  }

  interval = null

  start = () => {
    const {running} = this.state
    if (!running) {
      this.setState({
        running: true,
      })
      this.interval = setInterval(() => {
        this.setState(prev => ({
          time: prev.time + 1,
        }))
      }, 1000)
    }
  }

  stop = () => {
    clearInterval(this.interval)
    this.setState({running: false})
  }

  reset = () => {
    clearInterval(this.interval)
    this.setState({time: 0})
  }

  compoenetWillUnmount() {
    clearInterval(this.interval)
  }

  render() {
    const {time} = this.state
    const min = String(Math.floor(time / 60)).padStart(2, '0')
    const sec = String(time % 60).padStart(2, '0')
    return (
      <div className="cont">
        <div className="content">
          <h1 className="stop-watch-head">Stopwatch</h1>
          <div className="card">
            <div className="flex-row">
              <img
                alt="stopwatch"
                className="icon"
                src="https://assets.ccbp.in/frontend/react-js/stopwatch-timer.png "
              />
              <p className="timer">Timer</p>
            </div>
            <div>
              <h1>
                {min}:{sec}
              </h1>
              <div className="btn-cont">
                <button type="button" className="start" onClick={this.start}>
                  Start
                </button>
                <button type="button" className="stop" onClick={this.stop}>
                  Stop
                </button>
                <button type="button" className="reset" onClick={this.reset}>
                  Reset
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    )
  }
}
export default Stopwatch
